.Poster {
  margin-right: 4px;
  display: inline-block;
  transition: transform 300ms;
}

.Poster:hover {
  transform: scale(1.2);
  border-radius: 2%;
}

img {
  max-height: 169px;
  object-fit: contain;
  border-radius: 2%;
}

.imgAsPoster {
  max-height: 450px;
}

.posterOnHover {
  height: 100%;
  border-radius: 2%;
  font-size: 1em;
  font-weight: bolder;
  background-size: cover;
}
